export function movieListDataCreator (state, responseData, setter) {
    if (state === null) {
        setter(responseData);

    } 
    else {

        const stateIds = state.map(item => item.id);

        const matchheck = responseData.find(item => stateIds.includes(item.id));

        if (!matchheck) {
            setter([...state, ...responseData]);
        };
    }; 
};